/**
 * Transaction Service
 * AUDIT TASK #6: Payment transaction tracking for agency recoveries
 *
 * Manages financial transactions including:
 * - Agency commission calculations
 * - Net payments to freelancers
 * - Transaction history and audit trail
 * - Payment reconciliation
 *
 * Transaction Types:
 * - agency_recovery: Payment recovered by collection agency
 * - direct_payment: Direct payment from client to freelancer
 * - refund: Payment reversal
 */

import { db, Timestamp, FieldValue } from '@/lib/firebase';
import { logInfo, logError, logDbOperation } from '@/utils/logger';
import { nanoid } from 'nanoid';

/**
 * Agency Recovery Transaction
 * Tracks commission split and net payment
 */
export interface AgencyRecoveryTransaction {
  transactionId: string;
  transactionType: 'agency_recovery';

  // References
  invoiceId: string;
  freelancerId: string;
  agencyHandoffId: string;
  agencyId: string;

  // Amounts (in GBP)
  grossAmount: number; // Total amount recovered
  agencyCommissionRate: number; // e.g., 0.25 for 25%
  agencyCommissionAmount: number; // Calculated commission
  netToFreelancer: number; // Amount after commission

  // Status
  status: 'pending' | 'processing' | 'completed' | 'failed';

  // Payment Details
  paymentMethod?: 'bank_transfer' | 'check' | 'stripe';
  expectedPaymentDate?: Timestamp;
  actualPaymentDate?: Timestamp;

  // Banking/Transfer Info
  transferReference?: string;
  bankTransactionId?: string;

  // Metadata
  notes?: string;
  processedBy?: string; // Admin user who processed

  // Timestamps
  createdAt: Timestamp;
  updatedAt?: Timestamp;
  completedAt?: Timestamp;
}

/**
 * Create transaction record for agency recovery
 *
 * @param params Transaction parameters
 * @returns Transaction ID
 */
export async function createAgencyRecoveryTransaction(params: {
  invoiceId: string;
  freelancerId: string;
  agencyHandoffId: string;
  agencyId: string;
  grossAmount: number;
  agencyCommissionRate: number;
  notes?: string;
}): Promise<{
  success: boolean;
  transactionId?: string;
  error?: string;
}> {
  const startTime = Date.now();

  try {
    // Calculate commission and net amount
    const agencyCommissionAmount = params.grossAmount * params.agencyCommissionRate;
    const netToFreelancer = params.grossAmount - agencyCommissionAmount;

    // Create transaction document
    const transactionRef = db.collection('payment_transactions').doc();
    const transaction: AgencyRecoveryTransaction = {
      transactionId: transactionRef.id,
      transactionType: 'agency_recovery',

      invoiceId: params.invoiceId,
      freelancerId: params.freelancerId,
      agencyHandoffId: params.agencyHandoffId,
      agencyId: params.agencyId,

      grossAmount: params.grossAmount,
      agencyCommissionRate: params.agencyCommissionRate,
      agencyCommissionAmount,
      netToFreelancer,

      status: 'pending',
      notes: params.notes,

      createdAt: Timestamp.now(),
    };

    await transactionRef.set(transaction);

    logDbOperation('create_agency_recovery_transaction', 'payment_transactions', transactionRef.id, Date.now() - startTime);

    logInfo('Agency recovery transaction created', {
      transactionId: transactionRef.id,
      invoiceId: params.invoiceId,
      grossAmount: params.grossAmount,
      netToFreelancer,
    });

    return {
      success: true,
      transactionId: transactionRef.id,
    };

  } catch (error) {
    logError('Failed to create agency recovery transaction', error);
    return {
      success: false,
      error: error instanceof Error ? error.message : 'Unknown error',
    };
  }
}

/**
 * Update transaction status
 *
 * @param transactionId Transaction ID
 * @param status New status
 * @param metadata Additional metadata to update
 */
export async function updateTransactionStatus(
  transactionId: string,
  status: 'pending' | 'processing' | 'completed' | 'failed',
  metadata?: {
    transferReference?: string;
    bankTransactionId?: string;
    actualPaymentDate?: Date;
    processedBy?: string;
    notes?: string;
  }
): Promise<{
  success: boolean;
  error?: string;
}> {
  const startTime = Date.now();

  try {
    const updateData: any = {
      status,
      updatedAt: Timestamp.now(),
    };

    if (metadata) {
      if (metadata.transferReference) updateData.transferReference = metadata.transferReference;
      if (metadata.bankTransactionId) updateData.bankTransactionId = metadata.bankTransactionId;
      if (metadata.actualPaymentDate) updateData.actualPaymentDate = Timestamp.fromDate(metadata.actualPaymentDate);
      if (metadata.processedBy) updateData.processedBy = metadata.processedBy;
      if (metadata.notes) updateData.notes = metadata.notes;
    }

    if (status === 'completed') {
      updateData.completedAt = Timestamp.now();
    }

    await db.collection('payment_transactions').doc(transactionId).update(updateData);

    logDbOperation('update_transaction_status', 'payment_transactions', transactionId, Date.now() - startTime);

    logInfo('Transaction status updated', {
      transactionId,
      status,
    });

    return { success: true };

  } catch (error) {
    logError('Failed to update transaction status', error);
    return {
      success: false,
      error: error instanceof Error ? error.message : 'Unknown error',
    };
  }
}

/**
 * Get transaction details
 *
 * @param transactionId Transaction ID
 */
export async function getTransaction(
  transactionId: string
): Promise<AgencyRecoveryTransaction | null> {
  const startTime = Date.now();

  try {
    const doc = await db.collection('payment_transactions').doc(transactionId).get();

    logDbOperation('get_transaction', 'payment_transactions', transactionId, Date.now() - startTime);

    if (!doc.exists) {
      return null;
    }

    return doc.data() as AgencyRecoveryTransaction;

  } catch (error) {
    logError('Failed to get transaction', error);
    return null;
  }
}

/**
 * Get all transactions for a freelancer
 *
 * @param freelancerId Freelancer user ID
 * @param limit Maximum number of transactions to return
 */
export async function getFreelancerTransactions(
  freelancerId: string,
  limit = 50
): Promise<AgencyRecoveryTransaction[]> {
  const startTime = Date.now();

  try {
    const snapshot = await db
      .collection('payment_transactions')
      .where('freelancerId', '==', freelancerId)
      .orderBy('createdAt', 'desc')
      .limit(limit)
      .get();

    logDbOperation('get_freelancer_transactions', 'payment_transactions', undefined, Date.now() - startTime);

    return snapshot.docs.map(doc => doc.data() as AgencyRecoveryTransaction);

  } catch (error) {
    logError('Failed to get freelancer transactions', error);
    return [];
  }
}

/**
 * Get transactions for an agency handoff
 *
 * @param agencyHandoffId Agency handoff ID
 */
export async function getHandoffTransactions(
  agencyHandoffId: string
): Promise<AgencyRecoveryTransaction[]> {
  const startTime = Date.now();

  try {
    const snapshot = await db
      .collection('payment_transactions')
      .where('agencyHandoffId', '==', agencyHandoffId)
      .orderBy('createdAt', 'desc')
      .get();

    logDbOperation('get_handoff_transactions', 'payment_transactions', undefined, Date.now() - startTime);

    return snapshot.docs.map(doc => doc.data() as AgencyRecoveryTransaction);

  } catch (error) {
    logError('Failed to get handoff transactions', error);
    return [];
  }
}

/**
 * Calculate total recovered amount for a freelancer
 *
 * @param freelancerId Freelancer user ID
 */
export async function calculateFreelancerRecoveries(
  freelancerId: string
): Promise<{
  totalRecovered: number;
  totalCommission: number;
  totalNet: number;
  transactionCount: number;
}> {
  try {
    const transactions = await getFreelancerTransactions(freelancerId, 1000);

    const completed = transactions.filter(t => t.status === 'completed');

    const totalRecovered = completed.reduce((sum, t) => sum + t.grossAmount, 0);
    const totalCommission = completed.reduce((sum, t) => sum + t.agencyCommissionAmount, 0);
    const totalNet = completed.reduce((sum, t) => sum + t.netToFreelancer, 0);

    return {
      totalRecovered,
      totalCommission,
      totalNet,
      transactionCount: completed.length,
    };

  } catch (error) {
    logError('Failed to calculate freelancer recoveries', error);
    return {
      totalRecovered: 0,
      totalCommission: 0,
      totalNet: 0,
      transactionCount: 0,
    };
  }
}
