// Simple logger implementation to avoid Turbopack issues with Pino
// In production, consider using a proper logging service like Datadog, LogRocket, etc.

const LOG_LEVEL = process.env.LOG_LEVEL || 'info';
const LEVELS = { debug: 0, info: 1, warn: 2, error: 3 };

function shouldLog(level: keyof typeof LEVELS): boolean {
  return LEVELS[level] >= LEVELS[LOG_LEVEL as keyof typeof LEVELS];
}

export const logger = {
  info: (message: string, data?: object) => {
    if (!shouldLog('info')) return;
    if (data && Object.keys(data).length > 0) {
      console.log(`[INFO] ${message}`, data);
    } else {
      console.log(`[INFO] ${message}`);
    }
  },
  error: (message: string, data?: object) => {
    if (!shouldLog('error')) return;
    if (data && Object.keys(data).length > 0) {
      console.error(`[ERROR] ${message}`, data);
    } else {
      console.error(`[ERROR] ${message}`);
    }
  },
  warn: (message: string, data?: object) => {
    if (!shouldLog('warn')) return;
    if (data && Object.keys(data).length > 0) {
      console.warn(`[WARN] ${message}`, data);
    } else {
      console.warn(`[WARN] ${message}`);
    }
  },
  debug: (message: string, data?: object) => {
    if (!shouldLog('debug')) return;
    if (data && Object.keys(data).length > 0) {
      console.log(`[DEBUG] ${message}`, data);
    } else {
      console.log(`[DEBUG] ${message}`);
    }
  },
};

// Helper functions for structured logging
export function logInfo(message: string, data?: object) {
  logger.info(message, data);
}

export function logError(message: string, error?: Error | unknown, data?: object) {
  logger.error(
    message,
    {
      ...data,
      error: error instanceof Error ? {
        message: error.message,
        stack: error.stack,
        name: error.name,
      } : error,
    }
  );
}

export function logWarn(message: string, data?: object) {
  logger.warn(message, data);
}

export function logDebug(message: string, data?: object) {
  logger.debug(message, data);
}

// Log API request
export function logApiRequest(method: string, path: string, userId?: string) {
  logger.info('API Request', { method, path, userId });
}

// Log API response
export function logApiResponse(
  method: string,
  path: string,
  status: number,
  duration: number,
  userId?: string
) {
  logger.info(
    `API Response ${status}`,
    { method, path, status, duration, userId }
  );
}

// Log database operation
export function logDbOperation(
  operation: string,
  collection: string,
  documentId?: string,
  duration?: number
) {
  logger.debug(
    'Database Operation',
    { operation, collection, documentId, duration }
  );
}

// Log external API call
export function logExternalApiCall(
  service: string,
  operation: string,
  duration?: number,
  error?: Error
) {
  if (error) {
    logger.error(
      `External API Error: ${service}`,
      { service, operation, duration, error: error.message }
    );
  } else {
    logger.info(
      `External API Call: ${service}`,
      { service, operation, duration }
    );
  }
}
