/**
 * Firebase Storage Service
 * AUDIT TASK #5: Cloud document storage for agency handoff cases
 *
 * Handles upload and retrieval of:
 * - Invoice PDFs
 * - Communication history documents
 * - Supporting evidence for collection cases
 *
 * Security:
 * - All uploads are scoped to userId/handoffId for isolation
 * - Signed URLs expire after configurable time (default: 1 hour)
 * - Only authorized users (freelancer, agency, admin) can access
 */

import { getStorage } from 'firebase-admin/storage';
import { getApps } from 'firebase-admin/app';
import { logInfo, logError } from '@/utils/logger';

/**
 * Get Firebase Storage bucket
 * Uses default bucket from Firebase project
 */
function getStorageBucket() {
  if (!getApps().length) {
    throw new Error('Firebase is not initialized');
  }

  const storage = getStorage();
  const bucketName = process.env.FIREBASE_STORAGE_BUCKET || `${process.env.FIREBASE_PROJECT_ID}.appspot.com`;
  return storage.bucket(bucketName);
}

/**
 * Upload document to Firebase Storage
 *
 * @param params Upload parameters
 * @returns Public URL and storage path
 */
export async function uploadDocument(params: {
  fileBuffer: Buffer;
  fileName: string;
  contentType: string;
  handoffId: string;
  freelancerId: string;
  documentType: 'invoice' | 'communication' | 'evidence';
}): Promise<{
  success: boolean;
  storagePath?: string;
  publicUrl?: string;
  error?: string;
}> {
  try {
    const { fileBuffer, fileName, contentType, handoffId, freelancerId, documentType } = params;

    // Construct storage path: documents/{freelancerId}/{handoffId}/{documentType}/{fileName}
    const storagePath = `documents/${freelancerId}/${handoffId}/${documentType}/${fileName}`;

    const bucket = getStorageBucket();
    const file = bucket.file(storagePath);

    // Upload file with metadata
    await file.save(fileBuffer, {
      contentType,
      metadata: {
        handoffId,
        freelancerId,
        documentType,
        uploadedAt: new Date().toISOString(),
      },
    });

    logInfo('Document uploaded to Firebase Storage', {
      storagePath,
      fileName,
      handoffId,
      documentType,
    });

    // Make file publicly readable (could be restricted with Firebase rules)
    await file.makePublic();

    const publicUrl = `https://storage.googleapis.com/${bucket.name}/${storagePath}`;

    return {
      success: true,
      storagePath,
      publicUrl,
    };

  } catch (error) {
    logError('Failed to upload document to Firebase Storage', error);
    return {
      success: false,
      error: error instanceof Error ? error.message : 'Unknown error',
    };
  }
}

/**
 * Generate signed URL for secure document access
 *
 * Use this instead of public URLs for sensitive documents
 *
 * @param storagePath Path to file in storage
 * @param expiresInMinutes How long the URL should be valid (default: 60 minutes)
 * @returns Signed URL
 */
export async function getSignedUrl(
  storagePath: string,
  expiresInMinutes = 60
): Promise<{
  success: boolean;
  signedUrl?: string;
  expiresAt?: Date;
  error?: string;
}> {
  try {
    const bucket = getStorageBucket();
    const file = bucket.file(storagePath);

    // Check if file exists
    const [exists] = await file.exists();
    if (!exists) {
      return {
        success: false,
        error: 'File not found',
      };
    }

    const expiresAt = new Date();
    expiresAt.setMinutes(expiresAt.getMinutes() + expiresInMinutes);

    // Generate signed URL
    const [signedUrl] = await file.getSignedUrl({
      action: 'read',
      expires: expiresAt,
    });

    logInfo('Generated signed URL', {
      storagePath,
      expiresAt: expiresAt.toISOString(),
    });

    return {
      success: true,
      signedUrl,
      expiresAt,
    };

  } catch (error) {
    logError('Failed to generate signed URL', error);
    return {
      success: false,
      error: error instanceof Error ? error.message : 'Unknown error',
    };
  }
}

/**
 * Delete document from storage
 * Used for GDPR compliance and cleanup
 *
 * @param storagePath Path to file in storage
 */
export async function deleteDocument(storagePath: string): Promise<{
  success: boolean;
  error?: string;
}> {
  try {
    const bucket = getStorageBucket();
    const file = bucket.file(storagePath);

    // Check if file exists before deleting
    const [exists] = await file.exists();
    if (!exists) {
      logInfo('Document already deleted or does not exist', { storagePath });
      return { success: true };
    }

    await file.delete();

    logInfo('Document deleted from Firebase Storage', { storagePath });

    return { success: true };

  } catch (error) {
    logError('Failed to delete document from Firebase Storage', error);
    return {
      success: false,
      error: error instanceof Error ? error.message : 'Unknown error',
    };
  }
}

/**
 * List all documents for a handoff case
 *
 * @param handoffId Agency handoff ID
 * @param freelancerId Freelancer user ID
 */
export async function listHandoffDocuments(
  handoffId: string,
  freelancerId: string
): Promise<{
  success: boolean;
  documents?: Array<{
    name: string;
    storagePath: string;
    contentType: string;
    size: number;
    uploadedAt: Date;
    documentType: string;
  }>;
  error?: string;
}> {
  try {
    const bucket = getStorageBucket();
    const prefix = `documents/${freelancerId}/${handoffId}/`;

    const [files] = await bucket.getFiles({ prefix });

    const documents = files.map((file) => ({
      name: file.name.split('/').pop() || file.name,
      storagePath: file.name,
      contentType: file.metadata.contentType || 'application/octet-stream',
      size: parseInt(String(file.metadata.size || '0'), 10),
      uploadedAt: new Date(file.metadata.timeCreated || Date.now()),
      documentType: String(file.metadata.metadata?.documentType || 'unknown'),
    }));

    logInfo('Listed handoff documents', {
      handoffId,
      freelancerId,
      documentCount: documents.length,
    });

    return {
      success: true,
      documents,
    };

  } catch (error) {
    logError('Failed to list handoff documents', error);
    return {
      success: false,
      error: error instanceof Error ? error.message : 'Unknown error',
    };
  }
}

/**
 * Upload invoice PDF for handoff case
 * Convenience wrapper for invoice uploads
 */
export async function uploadInvoicePDF(params: {
  pdfBuffer: Buffer;
  invoiceReference: string;
  handoffId: string;
  freelancerId: string;
}): Promise<{
  success: boolean;
  storagePath?: string;
  publicUrl?: string;
  error?: string;
}> {
  const fileName = `invoice-${params.invoiceReference}.pdf`;

  return uploadDocument({
    fileBuffer: params.pdfBuffer,
    fileName,
    contentType: 'application/pdf',
    handoffId: params.handoffId,
    freelancerId: params.freelancerId,
    documentType: 'invoice',
  });
}

/**
 * Upload communication history document
 * Can be PDF, text, or JSON format
 */
export async function uploadCommunicationHistory(params: {
  contentBuffer: Buffer;
  fileName: string;
  contentType: string;
  handoffId: string;
  freelancerId: string;
}): Promise<{
  success: boolean;
  storagePath?: string;
  publicUrl?: string;
  error?: string;
}> {
  return uploadDocument({
    fileBuffer: params.contentBuffer,
    fileName: params.fileName,
    contentType: params.contentType,
    handoffId: params.handoffId,
    freelancerId: params.freelancerId,
    documentType: 'communication',
  });
}
